 /* App.css */

.home-page {
  text-align: center;
  margin: 20px;
  color: black;
}

.home-page{
  height:100vh;
  width:max;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(#7DF74B,#3EBB8C,#3EBB97); display: flex; 
  flex-direction: column;

}

.home-page h1 {
  font-size: 50px;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  margin-bottom: 10px;
}

.home-page p {
  font-size: 16px;
  margin-bottom: 20px;
}

.services {
  margin-bottom: 30px;
}

.services h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.services ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.services li {
  font-size: 14px;
  margin-bottom: 5px;
}

.about-us h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.about-us p {
  font-size: 14px;
  text-align: justify;
}

.contact-info h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

.contact-info p {
  font-size: 14px;
}

.btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  border-radius: 5px;
}

.btn:hover {
  background-color: #0056b3;
}

